import React, { Fragment, useEffect } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { getCurrentProfile, deleteAccount } from '../../actions/profile';
import Spinner from '../layout/Spinner';

import { Link } from 'react-router-dom';
import DashboardAction from './DashboardAction';
import Experience from './Experience';
import Education from './Education';
import Alert from '../layout/Alert';

const Dashboard = ({
    getCurrentProfile,
    deleteAccount,
    auth: { user },
    profile: { profile, loading },
}) => {
    useEffect(() => {
        getCurrentProfile();
    }, [getCurrentProfile]);

    return loading && profile === null ? (
        <Spinner />
    ) : (
        <Fragment>
            <h1 className='large text-primary' style={{ marginTop: "100px", marginLeft: "50px" }}>Dashboard</h1>
            <Alert />
            <p className='lead' style={{ marginLeft: "50px" }}>
                <i className='fas fa-user'></i> Welcome {user && user.name}
            </p>
            {profile !== null ? (
                <Fragment>
                    <div style={{ marginLeft: "50px" }}>
                        <DashboardAction />
                        <Experience experience={profile.experience} />
                        <Education education={profile.education} />
                        <div className='my-2'>
                            <button className='btn btn-danger' onClick={() => deleteAccount()}>
                                <i className='fas fa-user-minus'></i> Delete My Account
                            </button>
                        </div>
                    </div>
                </Fragment>
            ) : (
                <Fragment>
                    <p style={{ marginLeft: "50px" }}>You have not yet setup a profile, please add some info</p>
                    <Link to='/create-profile' className='btn btn-primary my-1' style={{ marginLeft: "50px" }}>
                        Create Profile
                    </Link>
                </Fragment>
            )}
        </Fragment>
    );
};

Dashboard.propTypes = {
    getCurrentProfile: PropTypes.func.isRequired,
    auth: PropTypes.object.isRequired,
    profile: PropTypes.object.isRequired,
    deleteAccount: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => ({
    auth: state.auth,
    profile: state.profile,
});

export default connect(mapStateToProps, { getCurrentProfile, deleteAccount })(
    Dashboard
);
